// team
.team-panel {
	position: absolute;
	z-index: 9999;
	left: 50%;
	top: 45%;
	background: #ffffff;
	transform: translate(-50%, -50%);
	max-height: 580px;
	min-width: 960px;
	display: inline-table;
	padding: 0;
	border: 1px solid #7c7c7c;
	-webkit-box-shadow: 5px 2px 20px 0px rgba(69,69,69,0.43);
	-moz-box-shadow: 5px 2px 20px 0px rgba(69,69,69,0.43);
	box-shadow: 5px 2px 20px 0px rgba(69,69,69,0.43);
	a {
		cursor: default;
	}
	.tool-bar {
		background: #0d52a1;
		width: 100%;
		height: 25px;
		.btn-close {
			position: absolute;
			z-index: 99;
			top: 0;
			right: 10px;
			background: none;
			height: 25px;
			width: 25px;
			padding: 2px;
			color: #ffffff;
			&:hover {
				background: #ffffff;
				color: #167df1;
				font-weight: 600;
			}
		}
		.btn-hide {
			position: absolute;
			z-index: 99;
			top: 0;
			right: 40px;
			background: none;
			height: 25px;
			width: 25px;
			padding: 2px;
			color: #ffffff;
			font-weight: 600;
			&:hover {
				background: #ffffff;
				color: #167df1;
			}
		}
	}
	.header-panel {
		display: inline-flex;
		width: 100%;
		height: 50px;
		background: #f2f2f2;
		border-bottom: 1px solid #c4c4c4;
		button {
			height: 100%;
			width: 100px;
			border-radius: 0;
			border-right: 1px solid #c4c4c4;
			position: relative;
			&:hover {
				background: #559ff3;
				color: #ffffff;
				font-weight: 600;
			}
			&:focus {
				background: #559ff3;
				color: #ffffff;
				font-weight: 600;
			}
			.num-commit {
				position: absolute;
				z-index: 999;
				right: 2px;
				top: 2px;
				width: 20px;
				height: 20px;
				font-size: 11px;
				padding: 2px;
				text-align: center;
				color: #ffffff;
				background: #c02323;
				border-radius: 50%;
			}
		}
		.btn-right {
			position: absolute;
			z-index: 99;
			right: 0;
			height: 50px;
			width: 100px;
			border-radius: 0;
			border-left: 1px solid #c4c4c4;
		}
		button.active {
			background: #167df1;
			color: #ffffff;
			font-weight: 600;
		}
	}
	.body-panel {
		width: 100%;
		height: 570px;
		display: flex;
		list-style: none;
		.left-panel {
			display: table-column;
			background: #f2f2f2;
			width: 30%;
			height: 570px;
			border-right: 1px solid #c4c4c4;
			a {
				font-weight: 400;
			}
			.title {
				font-weight: 600;
			}
			ul {
				padding-bottom: 15px;
				border-bottom: 1px solid #c4c4c4;
				padding: 0 0 10px;
				margin-bottom: 5px;
				li {
					padding: 0 15px;
					list-style: none;
					.btn-item {
						width: 100%;
						height: 100%;
						background: none;
						padding: 2px 5px;
						text-align: left;
						font-size: 13px;
						border-radius: 0;
						&:hover {
							background: #559ff3;
							color: #ffffff;
							font-weight: 600;
						}
						&:focus {
							background: #559ff3;
							color: #ffffff;
							font-weight: 600;
						}
					}
					.btn-item.selected {
						font-weight: 900;
					}
				}
			}
		}
		.right-panel {
			background: #ffffff;
			display: inline-grid;
			width: 70%;
			height: 100%;
			padding: 0;
			-webkit-box-shadow: inset 1px 1px 2px 1px rgba(140,140,140,0.5);
			-moz-box-shadow: inset 1px 1px 2px 1px rgba(140,140,140,0.5);
			box-shadow: inset 1px 1px 2px 1px rgba(140,140,140,0.5);
			.history-panel {
				height: 440px;
				overflow: hidden;
				overflow-y: scroll;
			}
			.commit-panel {
				background: #f2f2f2;
				display: inline-table;
				border-top: 1px solid #c4c4c4;
				padding: 5px 7px;
				height: 120px;
				textarea {
					width: 100%;
				}
				.btn-commit {
					float: right;
					background: #a7a7a7;
					color: #ffffff;
					width: auto;
					height: 25px;
					padding: 2px 10px;
					font-size: 13px;
				}
			}
		}
	}
}